<template>
	<div>
		<div class="accounts">
			<div class="head">
				<div class="head-content">
					<div class="head-left">
						<a href="javascript:void(history.back());" class="head-left--back back">
							<i class="m-icon m-icon-arrow-left"></i>
						</a>
					</div>
					<div class="head-title">
				      账号登录
				     </div>
				    <div class="head-left"></div>
				</div>
			</div>
		    <div class="account-content">
		    	<div class="account-group">
		    		<div class="meili-all-vue-base-field base-field input-field"  style="font-size: 0.4rem;">
		    			 <input type="text" placeholder="用户名/邮箱/手机号" class="base-field-core" v-model="uname" name="uname|iphone|email"> 
		    		</div>
		    		<div class="meili-all-vue-base-field base-field input-field"  style="font-size: 0.4rem;">
		    		 <input type="password" placeholder="密码" class="base-field-core" v-model="upwd" name="upwd"> 
		    		</div>
		    	</div>
		    	<div class="button" @click="login">
		    		<span class="button-text">登录</span>
		    	</div>
		    	<p class="account-links">
		    		<a href="javascript:;">忘记密码</a>
		    	</p>
		    </div>
		</div>
	</div>
</template>

<script>
	import { Toast } from 'mint-ui';
	export default{
		data(){
			return {
				uname:"",
				upwd:"",
				datas:[]
			}
		},
		methods:{
			login(){
				var _this=this;
				if(this.uname==""){
					Toast('请输入用户名/邮箱/手机');
				}else if(this.uname!="" && this.upwd==""){
					Toast('密码不能为空');
				}else{
//					this.$http.get("../../../static/Json/login.json").then(result=>{
//						if(result.body.status==1){
//							var obj=result.body.user.lists[0];
//							var {name,iphone,email}=result.body.user.lists[0];
                         
                          
				  var obj={
				 	"id":1,
				 	"name":"佘晓光",
				 	"iphone":17665437418,
				 	"password":123456,
				 	"head":"../../../static/img/icon/myavaet.png",
				 	"email":"731701904@qq.com" 	
				 };
                          var {name,iphone,email}=obj;
							for(var i in obj){
								if(obj[i]==_this.uname && _this.upwd==obj.password){
									Toast('登录成功');
									_this.$store.commit("islogin",false);
									_this.$store.commit("setuname",_this.uname);
									setTimeout(()=>{
									 _this.$router.push("/");
									},2000)
								}else if(obj[i]==_this.uname && _this.upwd!=obj.password){
									Toast('密码输入错误');
								}
							}
							if(_this.uname!=name && _this.uname!=iphone && _this.uname!=email){
								Toast('用户名未注册');
							}
							
//						}
//					})
				}
			}
		},
		mounted(){
			
		}
		
	}
</script>

<style scoped>
	.accounds{
		width:100%;
	}
	.head{
    height: 0.9rem;
    line-height: 0.9rem;
    width: 100%;
}
.head-content{
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    border-bottom: 1px solid #c9c7c8;
    height: 0.9rem;
    line-height: 0.9rem;
    width: 100%;
    background-color: #fafafa;
}
.head-left{
    position: relative;
    width: 0.9rem;
    text-align: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -moz-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -moz-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}
.back{
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -moz-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -moz-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}
.head-left--back{
    display: block;
    color: #5e5e5e;
    font-size: 0.2rem;
}
.m-icon{
    font-size: .35rem;
  /*  font-family: m-icon!important;*/
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.m-icon-arrow-left{
	display: inline-block;
	width:0.5rem;
	height:0.5rem;
	background:url(../../../static/img/icon/backleft.png) center center no-repeat;
	margin-top:0.15rem;
}
.head-title{
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -moz-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    text-align: center;
    font-size: 0.36rem;
    color: #5e5e5e;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.account-content{
    padding: 0 .5rem;
}
.account-group{
    margin: 1rem 0 .6rem;
}
.base-field {
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -moz-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    font-size: 0.28rem;
    color: #333;
    padding: 0.2rem 0;
    line-height: 1.5;
    border-bottom: 1px solid #ddd;
    background-color: #fff;
    position: relative;
}
.base-field-core {
    -webkit-box-flex: 1;
    -webkit-flex-grow: 1;
    -moz-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    border: none;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    font-size: inherit;
    color: inherit;
    line-height: 1.5;
    padding: 0 0.16rem;
    position: relative;
    z-index: 1;
    background-color: transparent;
}
.button{
    line-height: 1rem;
    border-radius: 1rem;
    background-color: #FF5777;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -moz-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -moz-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}
.button-text{
    font-size: 0.36rem;
    color: #fff;
    margin: 0 0.3rem;
}
.account-links{
    margin-top: .3rem;
}
.account-links a{
    font-size: .28rem;
    color: #999;
    float: right;
    padding: 0 .1rem;
}
</style>


