import Vue from 'vue'
import Router from 'vue-router'
import body from '@/components/home/body.vue'
import login from '@/components/login/login.vue'
import detail from '@/components/detail/detail.vue'
import accounts from '@/components/login/accounts.vue'
import shoppcar from '@/components/shoppcar/shoppcar.vue'
Vue.use(Router)

export default new Router({
  // mode: 'history',
  // base: __dirname,
  routes: [
    {  //首页
      path: '/',  //默认路径
      name: 'home',
      component:body 
    },
    {  //登录界面
    	path:'/login',
    	name:'login',
    	component:login
    },
    {  //商品详情
      path:'/detail',
      name:'detail',
      component:detail
      //路由嵌套
//    children:[{
//  		path:'/detail/shoppcar',
//  		name:shoppcar,
//  		component:shoppcar
//  	}]
    },
    {  //帐号登录
    	path:'/login/accounts',
    	name:'accounts',
    	component:accounts
    },
    {
    	path:'/detail/shoppcar',
    	name:shoppcar,
    	component:shoppcar
    }
  ]
})



