<template>
	<div>
		<div class="login">
			<div class="login-main" v-show="login">
				<h1 class="login-title">登录</h1>
				<div class=" button" style="background-color: rgb(9, 187, 7);">
					<img  src="../../../static/img/icon/wx.png" alt="icon" class="button-icon"> 
					<span  class="button-text">微信登录</span>
				</div>
				<div class="login-other">
					<h3 class="login-other-title">其他登录方式</h3>
					<ul  class="login-other-list m-row m-flex-around">
						<li class="login-other-item">
							<a>
							    <img src="../../../static/img/icon/qq.png" alt="image"> 
								<p>QQ登录</p>
						    </a>
						</li>
						<li class="login-other-item">
							<a>
								<img src="../../../static/img/icon/sj.png" alt="image"> 
							    <p>免密登录</p>
							</a>
						</li>
						<li class="login-other-item">
						    <router-link to="/login/accounts">
						    	<img src="../../../static/img/icon/zh.png" alt="image">
						    	<p>账号登录</p>
						    </router-link>
						</li>
					</ul>
				</div>
		    </div>
		    
		    <!--个人主页-->
		    <div v-show="!login" class="login-success">
		    	<!--头像信息-->
		    	<div class="head-box">
		    		<div class="profile-box">
		    			<div>
		    				<div class="user-avatar-wrap">
		    					<div class="user-avatar">
		    						<img src="../../../static/img/user/mytx.jpg">
		    					</div> 
		    					<img src="../../../static/img/user/v2-100x100.png" class="user-level">
		    				</div> 
		    				<div class="user-info">
		    					<span class="user-name">佘晓光</span>
		    					<a class="link" href="javascript:;">个人主页</a>
		    				</div>
		    			</div>
		    		</div>
		    	</div>
		    	
		    	<!--购物车-->
		    	<div class="collection-tips">
		    		<div class="item-wrap">
		    			<a  href="javascript:;" class="collection-header">
		    				<div class="item-text">
		    					<p class="item-icon">
		    						<img src="../../../static/img/icon/shoppcar.png">
		    					</p> 
		    					<div class="item-main">
		    						<p class="item-title">购物车</p> 
		    						<p class="coupon-num">
		    							<span></span>
		    						</p>
		    					</div>
		    				</div>
		    			</a>
		    		</div>
		    	</div>
		    	<!--退出登录-->
		    	<div class="out-content" @click="clearuser">
		    		<a  href="javascript:;" class="logout">退出登录</a>
		    	</div>
		    </div>
		</div>
	</div>
</template>

<script>
export default{
    data(){
        return {
          
        }
    },
    methods:{
       show(){
             this.$store.commit("ishead",false);
             this.$store.commit("isfooter",false);
       },
       //点击清除localStora用户
       clearuser(){
       	  this.$store.commit("islogin",true);
       	  this.$store.commit("setuname","");
       	  this.$router.push('/')
       }
    },
    mounted(){
        this.show()
//     console.log(this.$store.state.islogin)
    },
//  //计算属性
	  computed:{
	  	 login(){
	  	 	return this.$store.state.islogin
	  	 }
	  }
}
</script>

<style scoped>
	.login{
		background:#fff;
		width:100%;
		height:100%;
	}
	.login-main {
    width: 84%;
    margin: 0 auto;
    padding-bottom: 1rem;
    }
    .login-title{
    margin: 1.5rem 0 1rem;
    text-align: center;
    font-size: .56rem;
    font-weight: 500;
    color: #444;
    letter-spacing: .02rem;
   }
   .button {
    line-height: 1rem;
    border-radius: 1rem;
    /*background-color: #FF5777;*/
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -moz-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -moz-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}
.button-icon{
    width: 0.45rem;
}
.button-text{
    font-size: 0.36rem;
    color: #fff;
    margin: 0 0.3rem;
}
.login-other{
    margin-top: 1.5rem;
}
.login-other:before {
    content: "";
    display: block;
    width: .4rem;
    border-top: 1px solid #bbb;
    margin: 0 auto;
}
.login-other-title{
    font-size: .3rem;
    font-weight: 400;
    text-align: center;
    color: #999;
    margin: .8rem 0 .3rem;
}
.login-other-list{
    padding: .5rem 0;
}
.m-flex-around {
    justify-content: space-around;
}
.m-row {
    display: flex;
    flex: 0 1 auto;
    flex-flow: row wrap;
}
.login-other-item{
    position: relative;
    text-align: center;
}
.login-other-item img{
    width: .88rem;
}
.login-other-item p{
    margin-top: .18rem;
    font-size: .26rem;
    color: #666;
}

/*登录成功样式*/
.login-success{
	width:100%;
	overflow: hidden;
}
.head-box{
    background: #fff url("../../../static/img/icon/userbgcolor750x342.png") 0 -.44rem no-repeat;
    background-size: 7.5rem 3.42rem;
    height: 3.94rem;
    overflow: hidden;
}
.profile-box{
    margin: 1.35rem auto 0;
    height: 2.27rem;
    width: 6.78rem;
    border-radius: .1rem;
    -webkit-box-shadow: 0 0.02rem 0.32rem rgba(0,0,0,.12);
    box-shadow: 0 0.02rem 0.32rem rgba(0,0,0,.12);
    background: #fff;
    position: relative;
}
.profile-box .user-avatar-wrap{
    position: absolute;
    left: 3.39rem;
    top: -.65rem;
    margin-left: -.65rem;
    width: 1.3rem;
    height: 1.3rem;
}
.profile-box .user-avatar{
    border: .04rem solid #fff;
    border-radius: 100%;
    overflow: hidden;
    width: 1.3rem;
    height: 1.3rem;
}
.profile-box .user-avatar img{
    width: 100%;
    margin-bottom: .3rem;
}
.profile-box .user-level{
    position: absolute;
    right: 0;
    bottom: 0;
    width: .5rem;
    height: .5rem;
    overflow: hidden;
}
.profile-box .user-info{
    text-align: center;
    padding-top: .83rem;
}
.profile-box .user-info .user-name{
    font-size: .3rem;
    line-height: .42rem;
    font-weight: 700;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -moz-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -moz-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

.profile-box .user-info .link{
	margin:0 auto;
    display:block;
    margin-top: .18rem;
    height: .46rem;
    line-height: .46rem;
    border: .01rem solid #d2d2d2;
    border-radius: .22rem;
    width: 1.74rem;
    padding-left: .3rem;
    background: url("../../../static/img/icon/grzy.png") .22rem .08rem no-repeat;
    background-size: .21rem .26rem;
}

.collection-tips{
    width: 100%;
    background-color: #fff;
}
.collection-tips .item-wrap{
    margin-left: .35rem;
    border-bottom: 1px solid #e5e5e5;
}
.collection-tips .collection-header {
    display: block;
}
.collection-tips .collection-header .item-text{
    height: 1.11rem;
    background: url("../../../static/img/icon/userjt.png") no-repeat;
    background-position-x: 6.65rem;
    background-position-y: .43rem;
    background-size: .12rem;
    overflow: hidden;
}
.collection-tips .collection-header .item-text .item-icon{
    width: .74rem;
    height: 1.1rem;
    float: left;
}
.collection-tips .collection-header .item-text .item-icon img{
    display: block;
    width: .44rem;
    height: .44rem;
    margin: .28rem .3rem .28rem 0;
    text-align: center;
}
.collection-tips .collection-header .item-text .item-main{
    width: 6.06rem;
    height: 1.1rem;
    line-height: 1.1rem;
    font-size: .32rem;
    color: #333;
    float: left;
    white-space: nowrap;
}
.collection-tips .collection-header .item-text .item-main .item-title {
    float: left;
}
.collection-tips .collection-header .item-text .item-main .coupon-num {
    font-size: .24rem;
    max-width: 3.5rem;
    float: right;
    text-overflow: clip;
    overflow: hidden;
    white-space: nowrap;
    margin-right: .3rem;
}
.out-content {
    width: 7.5rem;
    margin-top: .3rem;
}
.out-content .logout {
    display: block;
    width: 6.6rem;
    height: .8rem;
    line-height: .8rem;
    color: #fff;
    background-color: #ff5777;
    font-size: .3rem;
    margin: 0 auto;
    border-radius: .04rem;
    text-align: center;
}
</style>


