import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

function storeLocalStore(state){
	 window.localStorage.setItem("msg",JSON.stringify(state));
}
export default new Vuex.Store({
    state:{
       ishead:true,
       islogin:true,
       isfooter:true,
       uname:"",
       data:[]
    },
    actions:{

    },
    mutations:{
//     ishome(state,bool){
//        this.state.ishome=bool;
//       console.log(this.state.ishome);
//     },
//     islogin(state){
//     	 this.state.islogin=!this.state.islogin
//     	 console.log(this.state.islogin)
//     },
       ishead(state,name) {
            state.ishead = name
            storeLocalStore(state)
        },
        islogin(state,name) {
            state.islogin = name
            storeLocalStore(state)
        },
        isfooter(state,name){
        	state.isfooter = name
            storeLocalStore(state)
        },
        setuname(state,name){
        	state.uname=name
        	storeLocalStore(state)
        },
        add(state,name){
        	state.data=name;
        	storeLocalStore(state);
        }
    }
})


