<template>
  <div id="app">
    <mgHeader v-show="ishead"></mgHeader>
    <router-view/>
    <mgFooter v-show="isfooter"></mgFooter>
  </div>
</template>
<script>
import mgHeader from "@/components/home/head.vue"
import mgFooter from "@/components/home/footer.vue"

//vuex提供了独立的构建工具函数 Vuex.mapState
import {mapState} from 'vuex'

export default {
  name: 'App',
  data(){
    return {

    }
  },
  components:{
    mgHeader,
    mgFooter
  },
  methods:{
     
  },
  mounted(){
    
  },
  created(){
  	//Object.assign(a,b) ==>b连接a,如果相同覆盖
  	localStorage.getItem("msg") && this.$store.replaceState(Object.assign(this.$store.state,JSON.parse(localStorage.getItem("msg"))));
  },
  //计算属性
//computed:mapState({
//  //映射 this.ishome 到 this.$store.state.ishome
//   ishome: state=>{
//   	  state.ishead,
//   	  state.isfooter
//   	 }
//})
  computed:{
  	ishead(){
  		return this.$store.state.ishead
  	},
  	isfooter(){
  		return this.$store.state.isfooter
  	}
  }
     

}
</script>

<style>
*{
  margin:0;
  padding:0;
}
/*mint-ui swipe样式修改*/ 
.mint-swipe-indicators{
   bottom:-0.15rem!important;
}
.mint-swipe-indicator{
  background:#fff!important;
  opacity:0.4!important;
}
.is-active{
  background:#f07!important;
}
</style>



