<script>
    export default{
    	data(){
    		return{
    			
    		}
    	},
        methods:{
          
        },
        mounted(){
        	
        }
    }
</script>
<style scoped>
	.sitting{
		 height:0.98rem;
    bottom: 0rem;
     width: 100%;
     border-top: 1px solid rgb(226, 226, 226);
     position: relative;
     bottom:0;
     z-index: -10;
	}
.nav_bottom_wrapper{
    background-color: rgb(255, 255, 255);
    height:0.98rem;
    bottom: 0rem;
