        background-size:0.5rem;
    }
    .good-icon{
        background:url("../../../static/img/icon/good.png") no-repeat center center;
        background-size:0.5rem;
    }
    .mooc-icon{
        background:url("../../../static/img/icon/mooc.png") no-repeat center center;
        background-size:0.5rem;
    }
    .shopping-icon{
        background:url("../../../static/img/icon/shopping1.png") no-repeat center center;
        background-size:0.5rem;
    }
    .home-active{
        background:url("../../../static/img/icon/at-home.png") no-repeat center center;
        background-size:0.5rem; 
    }
    .at-color{
        color:#1296db;
    }
</style>


