let LS=window.sessionStorage;
const LStorage={
	getItem(key){   
		try{
			return JSON.parse(LS.getItem(key));   //字符串转化为对象
		}catch(err){
			return null;
		}
	},
	setItem(key,val){
		LS.setItem(key,JSON.stringfiy(val));  //转为字符串
	},
	clear(){
		LS.clear();
	},
	removeItem(key){
		LS.removeItem(key);
	}
}

