           showshop(){
           	 this.addshop=!this.addshop
           	 setTimeout(()=>{
           	  	this.shownum = 0
           	  },200)
           },
           //购物车选项列表关闭
           closeshop(){
        	  this.addshop=false;
           	  setTimeout(()=>{
           	  	this.shownum = ""
           	  },200)
           },
           //改变购物车的数量
           changeNum(i,num){
           	  if(num>0){
           	  	this.i++;
           	  }else{
           	  	 this.i--;
           	  	 if(i<2){
           	  	   this.i=1;
           	  	 }
           	  }
           },
