//                          // _this.i = res.number
//                          _this.msg=res;
//                          console.log(_this.msg);
//                       }
//           });
           },
           selecttype(sele,index){
           	this.selectType=sele;
           	this.num = index
           	this.selectdata.type=sele;
           },
           selectsize(sele,index){
           	this.selectSize=sele;
           	this.size = index;
           	this.selectdata.size=sele;
           },
           addshoppcar(selectdata,i,buyprice,title,delprice,listpic){
           	  selectdata.i=i;
           	  selectdata.buyprice=buyprice;
           	    selectdata.title=title;
           	     selectdata.delprice=delprice;
           	    selectdata.listpic=listpic;
           	  //调用vuex方法
           	  this.$store.commit("add",selectdata);
//         	  console.log(selectdata+i+buyprice)
           }
           
        },
        created(){
         //  this.getshop(this.id);
        },
        mounted(){
            this.get();
        }
    }
</script>
