.goods-cont--presale,.goods-cont{
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-direction: normal;
    -moz-box-direction: normal;
}
.goods-cont{
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -moz-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    margin-left: .3rem;
    margin-right: .35rem;
    -webkit-box-orient: vertical;
    -webkit-flex-flow: column nowrap;
    -moz-box-orient: vertical;
    -ms-flex-flow: column nowrap;
    flex-flow: column nowrap;
    -webkit-box-align: start;
    -webkit-align-items: flex-start;
    -moz-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
}
.goods-cont--price{
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-flow: row nowrap;
    -moz-box-orient: horizontal;
    -moz-box-direction: normal;
    -ms-flex-flow: row nowrap;
    flex-flow: row nowrap;
    -webkit-box-align: start;
    -webkit-align-items: flex-start;
    -moz-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
}
.goods-cont--price-unit{
    font-size: .24rem;
    color: #333;
    margin-top: .15rem;
}
.goods-cont--price-text{
    font-size: .56rem;
    color: #333;
}
.goods-cont--price-unit{
    font-size: .24rem;
    color: #333;
    margin-top: .15rem;
}
.goods-cont--stock{
    font-size: .24rem;
    color: #333;
}
.goods-cont--addon{
    font-size: .24rem;
    color: #333;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: 4.5rem;
}
.goods-close{
    width: .3rem;
    font-size: .3rem;
    font-family: serif;
    position: absolute;
    top: .24rem;
    right: .32rem;
    color: #666;
}
.goods-close--icon{
    font-size: .3rem;
    display: inline-block;
    width:.3rem;
    height:.3rem;
    background:url("../../../static/img/icon/x.png") center center no-repeat;
}
.sku-content--scroll{
    overflow: auto;
    max-height: 6.5rem;
}
.scroll{
    padding: 0 .15rem;
    overflow: auto;
}
.prop {
    margin-bottom: .1rem;
}
.prop-rows {
    margin-top: .15rem;
}
.prop-rows--title {
    line-height: .24rem;
    font-size: .24rem;
}
.prop-rows--content {
    margin-top: .1rem;
}
ol {
    display: block;
    list-style-type: decimal;
    -webkit-margin-before: 1em;
    -webkit-margin-after: 1em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
   /* -webkit-padding-start: 40px;*/
}
.prop-item {
    display: inline-block;
    line-height: .52rem;
    padding: 0 .3rem;
    border: 1px solid #bbb;
    border-radius: .06rem;
    margin-right: .2rem;
    margin-bottom: .2rem;
    font-size: .24rem;
    cursor: pointer;
    color: #5e5e5e;
    white-space: nowrap;
    text-align: center;
}
.prop-item-active{
	display: inline-block;
    line-height: .52rem;
    padding: 0 .3rem;
    border: 1px solid #f07;
    border-radius: .06rem;
    margin-right: .2rem;
    margin-bottom: .2rem;
    font-size: .24rem;
    cursor: pointer;
    color: #f07;
    white-space: nowrap;
    text-align: center;
}
.number{
    padding: 0 .15rem;
    margin-bottom: .3rem;
}
.number-limit, .number-title {
    line-height: .24rem;
    font-size: .24rem;
}
.number-calculator{
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    width: 1.8rem;
    height: .5rem;
    border: 1px solid #bbb;
    border-radius: .06rem;
    margin-top: .15rem;
    text-align: center;
    line-height: .4rem;
}
.number-calculator--increase.is-disable,.number-calculator--reduce.is-disable{
    color: #999;
}
.number-calculator--increase,.number-calculator--reduce {
    display: block;
    width: .56rem;
    color: #ed4566;
    font-size: .32rem;
    cursor: pointer;
    padding-top:0.04rem;
}
.reduce-padding-top{
	padding-top:.05rem;
}
.number-calculator--value{
    display: block;
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -moz-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    border-left: 1px solid #ccc;
    border-right: 1px solid #ccc;
    color: #333;
    font-size: .28rem;
    padding-top:0.04rem;
}
.foot{
    height: 1rem;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-flow: row nowrap;
    -moz-box-orient: horizontal;
    -moz-box-direction: normal;
    -ms-flex-flow: row nowrap;
    flex-flow: row nowrap;
}
.foot a{
     display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    width:100%;
}
.foot-cart,.foot-okbtn{
    height: 1rem;
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -moz-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    line-height: 1rem;
    text-align: center;
    font-size: .28rem;
}
.foot-okbtn{
    display: block;
    background: -webkit-linear-gradient(left,#ff5777,#ff468f);
    background: linear-gradient(90deg,#ff5777,#ff468f);
    color: #fff;
}
</style>


